.form {
  display: grid;
  grid-template-columns: auto 150px;
  grid-gap: 20px;
  margin: 10px;
}

.form__input {
  padding: 10px;
  border: solid #cfcfcf 1px;
}

.form__submitButton {
  font-size: 18px;
  padding: 10px;
  background-color: #008080;
  color: #ffffff;
  border: none;
  cursor: pointer;
  transition: 0.5s;
}

.form__submitButton:hover {
  background-color: #00b3b3;
  transform: scale(1.1);
}

@media (max-width: 768px) {
  .form {
    margin: 10px;
    display: flex;
    flex-wrap: wrap;
  }

  .form__input {
    margin: 0 auto;
    min-width: 100%;
  }

  .form__submitButton {
    margin: 0 auto;
    margin-top: 10px;
    min-width: 100%;
  }

  .form__submitButton:hover {
    background-color: #00b3b3;
    transform: scaleX(1.02) scaleY(1.02);
  }
}
